.promotions__link_doc {
  display: block;
  width: 100%;
  color: #275957;
}
.promotions__logo-wrapper {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 75px;
  margin-left: auto;
  padding: 10px 7px;
  background: #275957;
}
@media screen and (min-width: 480px) {
  .promotions__logo-wrapper {
    max-width: 117px;
    padding: 10px;
  }
}
@media screen and (min-width: 576px) {
  .promotions__logo-wrapper {
    max-width: 160px;
  }
}
@media screen and (min-width: 768px) {
  .promotions__logo-wrapper {
    padding: 10px 27px;
  }
}
@media screen and (max-width: 854.98px) and (orientation: landscape) {
  .promotions__logo-wrapper {
    max-width: 130px;
    padding: 10px;
  }
}
@media screen and (max-width: 575.98px) and (orientation: landscape) {
  .promotions__logo-wrapper {
    max-width: 100px;
    padding: 10px;
  }
}
.promotions__logo-wrapper img {
  width: 100%;
  max-width: 100%;
  height: auto;
}
#promotionsCashback .tab__content {
  padding-right: 15px;
  padding-left: 15px;
}
@media screen and (min-width: 480px) {
  #promotionsCashback .tab__content {
    padding-right: 25px;
    padding-left: 25px;
  }
}
.acc__item {
  max-height: 0;
  height: auto;
  overflow: hidden;
  transition: max-height 0.3s;
}
.animation-glare:after {
  content: "";
  top: 0;
  left: -200%;
  display: block;
  width: 80px;
  height: 100%;
  position: absolute;
  background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 0, rgba(255, 255, 255, 0.4) 100%);
  background: -webkit-gradient(linear, left top, right top, color-stop(0, rgba(255, 255, 255, 0)), to(rgba(255, 255, 255, 0.4)));
  background: -o-linear-gradient(left, rgba(255, 255, 255, 0) 0, rgba(255, 255, 255, 0.4) 100%);
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0, rgba(255, 255, 255, 0.4) 100%);
  transform: skewX(-25deg);
  animation-name: glare_anim_right;
  animation-duration: 4s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
}
@keyframes accordionShow {
  0% {
    max-height: 0;
    opacity: 0;
  }
  100% {
    max-height: 100%;
    opacity: 1;
  }
}
@keyframes accordionHide {
  0% {
    max-height: 100%;
    opacity: 1;
  }
  100% {
    max-height: 0;
    opacity: 0;
  }
}
@keyframes treatment {
  0% {
    transform: rotateY(0deg);
  }
  100% {
    transform: rotateY(360deg);
  }
}
@keyframes expandUp {
  0% {
    transform: translateY(0) rotate(270deg);
  }
  50% {
    transform: translateY(-5px) rotate(270deg);
  }
  100% {
    transform: translateY(0) rotate(270deg);
  }
}
@keyframes expandDown {
  0% {
    transform: translateY(0) rotate(90deg);
  }
  50% {
    transform: translateY(5px) rotate(90deg);
  }
  100% {
    transform: translateY(0) rotate(90deg);
  }
}
@keyframes expandRight {
  50% {
    transform: translateX(5px);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes expandLeft {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(-5px);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes glare_anim_right {
  0% {
    left: -200%;
    opacity: 0;
  }
  70% {
    left: -200%;
  }
  100% {
    left: 200%;
    opacity: 1;
  }
}
@keyframes expandUpNoRotate {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-5px);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes expandDownNoRotate {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(5px);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes errorShow {
  0% {
    opacity: 0;
    visibility: hidden;
    top: 0;
    transform: none;
  }
  100% {
    opacity: 1;
    visibility: visible;
    top: 100%;
    transform: translateY(3px);
  }
}
@keyframes errorHide {
  0% {
    opacity: 1;
    visibility: visible;
    top: 100%;
    transform: translateY(3px);
  }
  100% {
    opacity: 0;
    visibility: hidden;
    top: 0;
    transform: none;
  }
}
.errorShow {
  animation-name: errorShow;
  animation-duration: 0.3s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: both;
}
.errorHide {
  animation-name: errorHide;
  animation-duration: 0.3s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: both;
}
@keyframes warningShow {
  0% {
    width: 100%;
    opacity: 1;
    visibility: hidden;
    margin-top: -100px;
  }
  100% {
    width: 100%;
    opacity: 1;
    visibility: visible;
    margin-top: -50px;
  }
}
@keyframes warningHide {
  0% {
    width: 100%;
    opacity: 1;
    visibility: visible;
    margin-top: -50px;
  }
  100% {
    width: 100%;
    opacity: 0;
    visibility: hidden;
    margin-top: -100px;
  }
}
@media screen and (min-width: 0) {
  @keyframes warningShowNumber {
    0% {
      opacity: 0;
      visibility: hidden;
      top: 0;
      transform: none;
    }
    100% {
      opacity: 1;
      visibility: visible;
      top: 100%;
      transform: translateY(36px);
    }
  }
}
@media screen and (min-width: 360px) {
  @keyframes warningShowNumber {
    0% {
      opacity: 0;
      visibility: hidden;
      top: 0;
      transform: none;
    }
    100% {
      opacity: 1;
      visibility: visible;
      top: 100%;
      transform: translateY(45px);
    }
  }
}
@media screen and (min-width: 451px) {
  @keyframes warningShowNumber {
    0% {
      opacity: 0;
      visibility: hidden;
      top: 0;
      transform: none;
    }
    100% {
      opacity: 1;
      visibility: visible;
      top: 100%;
      transform: translateY(43px);
    }
  }
}
@keyframes widthMax {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}
@keyframes widthMin {
  0% {
    width: 100%;
  }
  100% {
    width: 0;
  }
}
@keyframes hideElem {
  0% {
    opacity: 1;
  }
  100% {
    display: none;
    opacity: 0;
  }
}
@keyframes showElem {
  0% {
    opacity: 0;
  }
  100% {
    display: flex;
    opacity: 1;
  }
}
@keyframes trembling {
  0% {
    transform: rotate(0);
  }
  50% {
    transform: rotate(45deg);
  }
  100% {
    transform: rotate(0);
  }
}
@keyframes showElement {
  0% {
    opacity: 0;
    margin-top: -50px;
  }
  100% {
    opacity: 1;
    margin-top: 0;
  }
}
@keyframes hideElement {
  0% {
    opacity: 1;
    margin-top: 0;
  }
  100% {
    opacity: 0;
    transform: translateY(-50px);
    height: 0;
  }
}
.showElement {
  animation-name: showElement;
  animation-duration: 0.3s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: both;
}
.hideElement {
  animation-name: hideElement;
  animation-duration: 0.3s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: both;
}
@keyframes perspectiveVerticalIn {
  0% {
    transform: none;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  30% {
    transform: scale(1) rotateY(20deg);
    box-shadow: -20px 15px 50px rgba(0, 0, 0, 0.2), inset -90px 0 100px rgba(0, 0, 0, 0.15);
  }
  60% {
    transform: scale(1) rotateY(20deg);
    box-shadow: -20px 15px 50px rgba(0, 0, 0, 0.2), inset -90px 0 100px rgba(0, 0, 0, 0.15);
  }
  100% {
    transform: scale(1.07) rotateY(0deg);
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2), inset -90px 0 100px transparent;
  }
}
@keyframes pulseOpacity {
  0% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.2;
    transform: scale(0.98);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes scrollHeightDemo {
  from {
    top: -20px;
  }
  to {
    top: 0;
  }
}
@keyframes elementsDelete {
  from {
    opacity: 0.7;
    transform: scale(0.98);
  }
  to {
    opacity: 0.1;
    transform: scale(0);
  }
}
